(function($){
    $(function(){
        $(document).on('add_to_cart', function(response){
            console.log('frags are ', response);
        });


    });



    //display cart on hover or click
    $(document).on('mouseover','.bc-mnc__cart-link--show-details-on-hover', function (e) {

        var cart_details = $(this).siblings('.bc-mnc__cart-details').first();

        cart_details.fadeIn();


    });

    /**
     * This function is a helper to get the cart data of a product (its key, id, variation...)
     * It takes current_element, which is the element that receives the action (click, most of the cases)
     * and find .bc-mnc__cart-details--single-item then search for the hidden data section
     * .bc-mnc__cart-details--single-item__cart-info and grab the following attributes:
     * ---data-product-id
     * ---data-variation-id
     * ---data-product-quantity
     * ---data-key
     *
     * then return an array with such data
     *
     *
     * @param current_element jquery object
     */
    function get_product_cart_data(current_element)
    {
        var data_element = current_element.closest('.bc-mnc__cart-details--single-item').find('.bc-mnc__cart-details--single-item__cart-info');
        var cart_design_id = parseInt(current_element.closest('.bc-mnc__cart-details').attr('data-option-id'));

        console.log('design id is: ', cart_design_id);

        var product_container = current_element.closest('.bc-mnc__cart-details--single-item');
        return {
            'product_id' : parseInt(data_element.attr('data-product-id')),
            'variation_id' : parseInt(data_element.attr('data-variation-id')),
            'quantity' : parseInt(data_element.attr('data-product-quantity')),
            'cart_key' : data_element.attr('data-key'),
            'cart_design_id': cart_design_id,
            'product_container': product_container
        }

    }

    //bc-mnc__cart-link--show-details-on-click
    $(document).on('click','.bc-mnc__cart-link--show-details-on-click', function (e) {

        e.preventDefault();
        $(this).siblings('.bc-mnc__cart-details').first().fadeIn();
    });

    //close button
    $(document).on('click', '.bc-mnc__cart-details--close-button', function(e){
        $(this).closest('.bc-mnc__cart-details').fadeOut();
    });


    /**
     * On use click on the remove button of the product on cart items list. First, hide it, then send ajax request
     */
    $(document).on('click', '.bc-mnc__cart-details--remove-product-icon', function() {
        var the_remove_button = $(this);

        var cart_data = get_product_cart_data(the_remove_button);


        update_product_amount(0, cart_data.cart_key, cart_data.product_id, cart_data.cart_design_id, cart_data.product_container);

    });


    function get_current_quantity_in_product_change_box(clicked_element)
    {
        var current_amount = parseInt(clicked_element
            .closest('.bc-mnc__cart-details--single-item__product-quantity')
            .find('.bc-mnc__cart-details--single-item__product-quantity--count')
            .text()
        );

        console.log('current amount is: ', current_amount);

        return current_amount;
    }

    function update_quantity_in_product_change_box(amount, clicked_element)
    {
        if (amount < 0)
            return;
        clicked_element
            .closest('.bc-mnc__cart-details--single-item__product-quantity')
            .find('.bc-mnc__cart-details--single-item__product-quantity--count')
            .text(amount)
    }


    $(document).on('click', '.bc-mnc__cart-details--single-item__product-quantity--decrease', function(){
        var current_amount = get_current_quantity_in_product_change_box($(this));
        var cart_data = get_product_cart_data($(this));

        var new_amount = current_amount - 1;
        update_quantity_in_product_change_box(new_amount, $(this));
        update_product_amount(new_amount, cart_data.cart_key, cart_data.product_id, cart_data.cart_design_id, cart_data.product_container);


    });

    $(document).on('click', '.bc-mnc__cart-details--single-item__product-quantity--increase', function(){
        var current_amount = get_current_quantity_in_product_change_box($(this));
        var cart_data = get_product_cart_data($(this));
        var new_amount = current_amount + 1;

        update_quantity_in_product_change_box(new_amount, $(this));

        update_product_amount(new_amount, cart_data.cart_key, cart_data.product_id, cart_data.cart_design_id, cart_data.product_container);


    });


    function update_product_amount(new_amount, cart_key, product_id, cart_design_id, product_container)
    {

        product_container.addClass('bc-loading');
        $.ajax({
            type: 'POST',
            dataType: 'json',
            url: wc_add_to_cart_params.ajax_url,
            data:
                {
                    'action': 'bc_menu_cart_product_change_amount',
                    'product_id':  product_id,
                    'cart_item_key': cart_key,
                    'new_amount' : new_amount,
                    'cart_design_id' : cart_design_id
                },

            success: function(response)
            {
                if (new_amount <= 0)
                    product_container.fadeOut();
                _.each(response.fragments, function(item, index){
                    $(index).replaceWith(item);
                });

            }
        });

    }
    /**
     * on mouse move out of the cart details hide it
     */

    // $(document).on('mouseleave', '.bc-mnc__cart-details', function(){
    //     $(this).fadeOut();
    // });


})(jQuery);